/*----*/

/*Общие стили*/

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  min-width: 319px;
}

.wrapper {
  max-width: 1230px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.wrap {
  max-width: 1170px;
  margin-left: auto;
  margin-right: auto;
  font-size: 0;
}

.ears::after {
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  position: absolute;
  top: 0;
  z-index: -1;
  margin: auto;
  left: -50vw;
  right: -50vw;
}

img {
  max-width: 100%;
  height: auto;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  cursor: pointer;
  outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

.d-none {
  display: none;
}

.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

/*--ТВИМ--*/

/*Общее для сайта*/

.headerBlock__img {
  display: block;
}

/*-------------------------------*/

.topSection {
  background-color: #00b9ee;
}

/*-------------------------------*/

.orderInstSection {
  background-color: #ffffff;
}

.order__btn {
  font-size: 18px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 160px;
  height: 40px;
  text-transform: uppercase;
  color: #ffffff;
  border-radius: 27px;
  border: none;
  background-color: #f39200;
  cursor: pointer;
}

.order__btn--green {
  background-color: #b7db2a;
}

/*-------------------------------*/

@media screen and (min-width: 320px) {
  .wrap {
    max-width: 320px;
  }

  .header {
    position: fixed;
    display: none;
    font-size: 0;
    z-index: 999999;
    min-width: 100%;
    background-color: #ffffff;
    opacity: 1;
    transition: min-height ease 0.5s, opacity ease 0.5s;
  }

  .header.active {
    display: block;
    opacity: 1;
  }

  .headerBlock {
    display: flex;
    justify-content: space-between;
    padding-top: 7px;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 10px;
    background-color: #fff;
    box-shadow: 0 7px 7px 0 rgba(0, 0, 0, 0.1);
  }

  .btnOval {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 129px;
    height: 37px;
    margin-top: 5px;
    font-family: 'Segoe UI', sans-serif;
    font-weight: normal;
    font-style: normal;
    color: #ffffff;
    font-size: 12.2px;
    text-transform: uppercase;
    border-radius: 25px;
    border: 3px solid #fff;
    background-color: #b7db2a;
    cursor: pointer;
  }

  .btnOval--bigDown {
    font-size: 18px;
    margin-left: auto;
    margin-right: auto;
    position: absolute;
    width: 175px;
    height: 46px;
    bottom: -68px;
    left: 0;
    right: 0;
    margin: 0 auto;
    cursor: pointer;
  }

  .topSection {
    min-height: 367px;
  }

  .topSectionBlock {
    position: relative;
    display: flex;
    flex-direction: column;
    text-align: center;
    padding-top: 20px;
    font-family: 'Segoe UI', sans-serif;
    font-weight: normal;
    font-style: normal;
    font-size: 16px;
    color: #ffffff;
  }

  .topSectionBlock p {
    margin-top: 10px;
    margin-bottom: 20px;
    line-height: 1.2;
  }

  .topSectionBlock a {
    font-family: 'Segoe UI', sans-serif;
    font-weight: normal;
    font-style: normal;
    font-size: 14px;
    color: #ffffff;
  }

  .topSectionBlock__title {
    margin-top: 21px;
  }

  .orderInstBlock {
    padding-top: 48px;
    line-height: 1.2;
  }

  .order {
    font-family: 'Segoe UI', sans-serif;
    font-weight: normal;
    font-style: normal;
    font-size: 18px;
    color: #363636;
    text-align: center;
  }

  .order p {
    margin-bottom: 20px;
  }

  .order b {
    font-family: 'Segoe UI', sans-serif;
    font-weight: bold;
    font-style: normal;
    color: #00b9ee;
  }

  .order:last-child {
    padding-top: 20px;
  }

  .order:last-child p {
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    width: 200px;
    line-height: 1.3;
  }

  .order:last-child p span {
    position: relative;
    display: inline-block;
    font-size: 0;
    width: 100px;
    height: 27px;
  }

  .order:last-child p span img {
    position: absolute;
    bottom: -5px;
    left: 2px;
  }
}

@media screen and (min-width: 750px) {
  .wrap {
    max-width: 750px;
  }
}

@media screen and (min-width: 1170px) {
  .wrap {
    max-width: 1170px;
  }
}

@media screen and (max-width: 920px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (max-width: 576px) {
  .wrapper {
    padding-left: 0px;
    padding-right: 0px;
  }
}

@media screen and (max-width: 480px) {
  .wrapper {
    padding-left: 0px;
    padding-right: 0px;
  }
}