@import "fonts.less";
@import "project.less";
@import "mix.less";
@import "colors.less";
@import "smart-grid.less";

/*Общие стили*/

*{
    box-sizing: border-box;
}

html{
    //overflow-x: hidden;
}

body{
    margin: 0;
    padding: 0;
    min-width: 319px;
}

.wrapper{
    .wrapper();
}

.wrap{
    max-width: 1170px;
    .mlr-a();
    .fz0();
}

.ears::after{
    content: '';
    display: block;
    height: 100%;
    width: 100vw;
    background-color: inherit;
    position: absolute;
    top: 0;
    z-index: -1;
    margin: auto;
    left: -50vw;
    right: -50vw;
}

img{
    max-width: 100%;
    height: auto;
}

ul{
    .mp0();
}

li{
    list-style: none;
}

a{
    text-decoration: none;
    cursor: pointer;
    outline: none;
}

h1, h2, h3, h4, h5, h6, p {
    .mp0();
}

.d-none{
    .d-none();
}

.clearfix::after {
    content: "";
    display: table;
    clear: both;
}

/////////////////////////////////////////
/*--ТВИМ--*/
/////////////////////////////////////////

/*Общее для сайта*/
.wrap{
    .from(320px,{
        max-width: 320px;
    });
    .from(750px,{
        max-width: 750px;
    });
    .from(1170px,{
        max-width: 1170px;
    });
}

.header{
    .from(320px,{
        position: fixed;
        display: none;
        font-size: 0;

        z-index: 999999;

        min-width: 100%; background-color: @white;

        opacity: 1;
        transition: min-height ease .5s, opacity ease .5s;
    });
}

.header.active{
    .from(320px,{
        display: block;
        opacity: 1;
    });
}

.headerBlock{
    .from(320px,{
        display: flex;
        justify-content: space-between;

        padding-top: 7px;
        padding-left: 15px;
        padding-right: 15px;
        padding-bottom: 10px;

        background-color: #fff;
        box-shadow: 0 7px 7px 0 rgba(0,0,0, .1);
    });
}

.headerBlock__img{
    display: block;
}

.btnOval{
    .from(320px,{
        display: flex;
        align-items: center;
        justify-content: center;

        width: 129px;
        height: 37px;

        margin-top: 5px;

        .SegoeUIReg();
        color: @white;
        font-size: 12.2px;

        text-transform: uppercase;

        border-radius: 25px;
        border: 3px solid #fff;
        background-color: #b7db2a;

        cursor: pointer;

    });
}

.btnOval--bigDown {
    .from(320px,{
        font-size: 18px;
        .mlr-a();

        position: absolute;

        width: 175px;
        height: 46px;

        bottom: -68px;
        left: 0;
        right: 0;
        margin: 0 auto;

        cursor: pointer;
    });
}

/*-------------------------------*/

.topSection{
    background-color: @blue;
    .from(320px,{
        min-height: 367px;
    });
}

.topSectionBlock{
    .from(320px,{
        position: relative;

        display: flex;
        flex-direction: column;

        text-align: center;

        padding-top: 20px;

        .SegoeUIReg();
        font-size: 16px;
        color: @white;

        p{
            margin-top: 10px;
            margin-bottom: 20px;
            line-height: 1.2;
        }
        a{
            .SegoeUIReg();
            font-size: 14px;
            color: @white;
        }
    });
}

.topSectionBlock__logo{
    .from(320px,{

    });
}

.topSectionBlock__title{
    .from(320px,{
        margin-top: 21px;
    });

}

/*-------------------------------*/

.orderInstSection{
    background-color: @white;
}

.orderInstBlock{
    .from(320px,{
        padding-top: 48px;
        line-height: 1.2;
    });
}

.order {
    .from(320px,{
        .SegoeUIReg();
        font-size: 18px;
        color: @grey36;

        text-align: center;

        p{
            margin-bottom: 20px;
        }
        b{
            .SegoeUIBold();
            color: @blue;
        }

        &:last-child{
            padding-top: 20px;
            p{
                .mlr-a();
                text-align: center;
                width: 200px;
                line-height: 1.3;
                //position: relative;

                span{
                    position: relative;

                    display: inline-block;
                    font-size: 0;
                    width: 100px;
                    height: 27px;

                    img{
                        position: absolute;
                        bottom: -5px;
                        left: 2px;
                    }
                }
            }
        }
    });

}

.order__btn{
    font-size: 18px;
    .mlr-a();

    display: flex;
    align-items: center;
    justify-content: center;

    width: 160px;
    height: 40px;

    text-transform: uppercase;

    color: @white;
    border-radius: 27px;
    border: none;
    background-color: #f39200;

    cursor: pointer;
    
    &--green{
        background-color: #b7db2a;
    }
}



